.attachments-page {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin: 0;
}
.attachments-container {
  margin-top: 20px;
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  box-sizing: border-box;
  background-color: rgb(185, 183, 183);
  border-radius: 2%;
  box-shadow: 8px 8px 24px -12px rgba(66, 68, 90, 1);
  max-width: 600px;
}

.attachments-select {
  width: 50%;
  padding: 10px;
  margin-bottom: 20px;
  font-size: 16px;
}

.attachments-list {
  width: 50%;
  display: flex;
  flex-direction: column;
}

.attachment-item {
  padding: 10px;
  margin-bottom: 10px;
  background-color: #f5f5f5;
  cursor: pointer;
  border-radius: 5px;
  text-align: center;
}

.attachment-item:hover {
  background-color: #e0e0e0;
}
.email-input {
  width: 50%;
  padding: 10px;
  margin-bottom: 20px;
  font-size: 16px;
  border-radius: 7px;
}
.help {
  color: white;
  font-size: 13pt;
  border: 2px solid #ddd;
  border-radius: 10px;
  padding: 20px;

  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
  line-height: 1.6;
  margin: 0 auto;
  max-width: 800px;
  margin-bottom: 20px;
}
